import React, { Component } from 'react';
import axios from 'axios';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import Header from '../../Header';
import {Link} from 'react-router-dom';
import '../details/Hoteldetails.css'
// import Hoteldetails from '../details./Hoteldetails';

const url = "https://jatul.herokuapp.com/rest";

class Details extends Component {
    constructor() {
        super()

        this.state = {
            details: ''
        }
    }
    render() {
        let { details } = this.state
        return (
            <>
            <Header/>
            <hr/>
            <div className="container" style={{fontFamily:"Times New Roman, Times, serif"}}>
                
                <div className="p-3 mb-2 bg-danger text-white">
                    <div className="panel-heading" >
                        <h3>{this.state.details.name} </h3>
                    </div>
                    <div className="panel-body">
                        <div className="row">
                            <div className="col-md-12">
                                <img className="img-fluid" src={details.thumb} />
                            </div>
                            <div className="col-md-6">
                                <h3>{details.name}</h3>
                                <h3>{details.locality}</h3>
                                <h3>{details.address}</h3>
                            </div>
                        </div>
                        <hr />
                        <Tabs>
                            <TabList>
                                <Tab>About</Tab>
                                <Tab>Contact</Tab>
                            </TabList>

                            <TabPanel>
                                <h2>About this restaurant</h2>
                                <p>{details.name} Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries,</p>
                            </TabPanel>
                            <TabPanel>
                                <h2>Contact us</h2>
                                <p>{details.address}</p>
                                <p>Phone No: 8130080818</p>
                            </TabPanel>
                        </Tabs>
                        <Link to="/" className="btn btn-danger">Back</Link> &nbsp;
                        <Link to={`/booking/${details.name}`} className="btn btn-success">Procceed Order</Link>
                    </div>
                </div>
            </div>
            </>
        )

    }
    componentDidMount() {
        const hotelId = this.props.match.params.id;
        axios.get(`${url}/${hotelId}`)
            .then((res) => { this.setState({ details: res.data[0] }) })
    }
}
export default Details;