import React from 'react';
import { Link } from 'react-router-dom';
import './Header.css';


const Header = (props) => {
    return (
        <>
            <div id="header">
                    <Link className="logo" to="/" style={{fontFamily:"Times New Roman,Times,serif"}} >e!</Link>
                    <span className="logoname" style={{ float: 'right',fontFamily:"Times New Roman,Times,serif",color:"white",marginTop:"2rem" }}>jatul kumar</span>
                </div>
        </>

    )
}
export default Header;