import React, { Component } from 'react';
import axios from 'axios';
import '../Home/listing/listing.css';
const url="https://jatul.herokuapp.com/rest?mealtype="
class CuisineFilter extends Component {
    CusineFilter =(event) =>{
        let mealId= sessionStorage.getItem('mealid')
        let cuisineId = event.target.value;
        let cuisineUrl;
        if(cuisineId==''){
            cuisineUrl=`${url}${mealId}`
        }else {
            cuisineUrl=`${url}${mealId}&cuisine=${cuisineId}`
        }
        axios.get(cuisineUrl)
        .then((response)=>{this.props.restPerCuisine(response.data)})
    }
    render() {
        return (
              
            <React.Fragment >
                <h3 className="cuisine">Cuisine</h3>
                <div className="filters" onChange ={this.CusineFilter}>
                    <label className="radiobtn">
                    <input type="radio" value="1" name="cuisine"/> North Indian
                    </label>
                    <br/>
                    <label className="radiobtn">
                    <input type="radio" value="2" name="cuisine"/> South Indian
                    </label>
                    <br/>
                    <label className="radiobtn">
                    <input type="radio" value="3" name="cuisine"/> Fast Food
                    </label>
                    <br/>
                    <label className="radiobtn">
                    <input type="radio" value="4" name="cuisine"/> Street Food
                    </label>
                    <br/>
                    <label className="radiobtn">
                    <input type="radio" value="5" name="cuisine"/> Chinese
                    </label>
                    
                    
      </div>
            </React.Fragment>
        )
    }
}
export default CuisineFilter;