import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import './Search.css';

const url = "https://jatul.herokuapp.com/city";
const rurl = "https://jatul.herokuapp.com/rest?city="

class Search extends Component {

    constructor(props) {
        super(props)
        this.state = {
            city: '',
            rest: ''
           
        }
    }
    //city option drop down
    renderCity = (data) => {
              
        if (data) {
            return data.map((item) => {
                return (
                    <option value={item.city}>{item.name} | {item.city_name}</option>
                )
            })
        }
    }
    handleCity = (event) => {
        console.log(event.target.value)
        const cityid = event.target.value;
        fetch(`${rurl}${cityid}`, { method: 'GET' })
            .then((res) => res.json())
            .then((data) => {
                this.setState({ rest:data })
            })
    }
    renderRest = (data) => {
        if (data) {
            return data.map((item) => {
                return (
                    <option value={item._id}>{item.name} | {item.locality} </option>
                )
            })
        }
    }
    
    handleRest=(event) => {
        this.props.history.push(`/details/${event.target.value}`)
    }
    
    render() {
        console.log(this.props);
        return (
            <React.Fragment>
                <div className="imageContainer">
                    <div style={{ textAlign: 'right' }}>
                        
                        <a className="fb myfont" href="https://www.facebook.com/" target="_blank">
                            <img src="/image/facebook.png" className="social_logo" />
                        </a>
                        <a className="yt myfont" href="https://www.google.com/" target="_blank">
                            <img src="/image/google-plus.png" className="social_logo" />
                        </a>
                    </div>
                    <div id="logo" >
                        <b>e!</b>
                    </div>
                    <div id="heading">
                        Find Bést Restaurants, Cafés, bars
            </div>
                    <div className="locationSelector">
                        <select className="dropdown" onChange={this.handleCity}style={{color:'GrayText',border:"None",borderRadius:2,height:35,textAlign:'center',fontFamily:"Times New Roman, Times, serif"}}>
                            <option value="1">----Select city----</option>
                            {this.renderCity(this.state.city)}
                        </select>
                    
                        <select className="dropdown" onChange={this.handleRest} style={{color:'GrayText',border:"None",borderRadius:2,height:35,textAlign:'center',fontFamily:"Times New Roman, Times, serif",marginLeft:4}}>
                               <option>-----Select rest-----</option> 
                            {this.renderRest(this.state.rest)}
                        </select>
                    </div>
                </div>
                </React.Fragment>
        )
    }
    //calling apis from bakend to get data
    componentDidMount() {
                    fetch(url,{method:'GET'})
                    .then((res)=> res.json())
                    .then((data)=> this.setState({city:data}))
            }
        }

export default withRouter(Search);