import React, { Component } from 'react';
import axios from 'axios';
import ListingDisplay from './listingDisplay';
import Header from '../../../Header';
import CusineFilter from '../../filters/cuisineFilter';
import CostFilter from '../../filters/costFilter';
import Sortfilter from '../../filters/sortfilter'
import './listing.css';

const url = "https://jatul.herokuapp.com/rest?mealtype=";
class ListingApi extends Component {
    constructor(props) {
        super(props)
        this.state = {
            restlist: ''
        }
    }
    setDatePerFilter=(sortedData)=>{
        this.setState({restlist:sortedData})
    }

    render() {
        console.log(this.props.match.params.id);
        return (
            <div className="row">
                <Header/>
                <div className="col-md-2"  id="filterbox">
                    <CusineFilter restPerCuisine={(data)=>{this.setDatePerFilter(data)}}/>
                    <CostFilter restPercost={(data)=>{this.setDatePerFilter(data)}}/>
                    <Sortfilter restPerSort={(data)=>{this.setDatePerFilter(data)}}/>
                 </div>
                <div className="col-md-10" id="listingbox">
                    <ListingDisplay retaurentList={this.state.restlist} />
                </div>

            </div>
        )
    }
    componentDidMount() {
        var mealid = this.props.match.params.id
        sessionStorage.setItem('mealid',mealid)
        axios.get(`${url}${mealid}`)
            .then((Response) => { this.setState({ restlist: Response.data }) })
    }

}
export default ListingApi;