import React from 'react';
import { Link } from 'react-router-dom';
import './listing.css';

const ListingDisplay = (props) => {
    const renderList =({retaurentList})=>{
        if(retaurentList){
            if(retaurentList.length>0){
                return retaurentList.map((item)=>{
                    return(
                        <div className="first">
                            <div class="maindiv">
                                <div class="image">
                                    <img src={item.thumb} className="imgclass" height="100px" width="100px" />
                                </div>
                                <div class="details">
                                   <Link to={`/details/${item._id}`}> <h1>{item.name}</h1></Link>
                                    <p>{item.city_name}</p>
                                    <p>₹ {item.cost} as per two</p>
                                    <hr/>   
                            <div class="moredetails">
                                <h4>Address</h4>
                                <p className="locality">{item.locality}</p>
                                <p className="locality1">{item.address}</p>
                            </div>
                                </div>
                            </div>
                               
                        </div>
                    
                    )
                })
            }else{
                return(
                    <div>
                        <center>
                            <h2>No restaurent found</h2>
                        </center>
                    </div>
                )
            }
            
        }else
        {
            return(
                <div>
                     <img src='/loader.gif'/>
                </div>
            )
        }
    }
    return (
        <>
        {renderList(props)}
        </>
    )

}
export default ListingDisplay;