import React, { Component } from 'react';
import axios from 'axios';
import './cuisine&cost.css';
const url="https://jatul.herokuapp.com/rest?mealtype="
class Sortfilter extends Component {
    sortfilter =(event) =>{
        let mealId= sessionStorage.getItem('mealid')
        let sort = event.target.value;
        const costUrl=`${url}${mealId}&sort=${sort}`
        axios.get(costUrl)
        .then((response)=>{this.props.restPerSort(response.data)}) 
        
    }
    render() {
        return (

            <div>
                <h3 className="cuisine">Sort</h3>
                <div className="filters" onChange={this.sortfilter}>
                    <label className="radiobtn">
                    <input type="radio" value="1" name="cuisine"/> Low to High
                    </label>
                    <br/>
                    <label className="radiobtn">
                    <input type="radio" value="-1" name="cuisine"/> High to Low
                    </label> 
      </div>
            </div>
        )
    }
}
export default Sortfilter;