import React, { Component } from 'react';
import axios from 'axios';
import '../Home/listing/listing.css';
const url="https://jatul.herokuapp.com/rest?mealtype="
class CostFilter extends Component {
    CostFilter =(event) =>{
        let mealId= sessionStorage.getItem('mealid')
        let cost = (event.target.value).split(',');
        let lcost=cost[0];
        let hcost =cost[1];
        let costUrl;
        if(cost==''){
            costUrl=`${url}${mealId}`
        }else {
            costUrl=`${url}${mealId}&lcost=${lcost}&hcost=${hcost}`
        }
        axios.get(costUrl)
        .then((response)=>{this.props.restPercost(response.data)})
    }
    render() {
        return (

            <React.Fragment>
                <h3 className="cuisine">Cost</h3>
                <div className="filters" onChange={this.CostFilter}>
                    <label className="radiobtn">
                    <input type="radio" value="0,100" name="cuisine"/> 0-100
                    </label>
                    <br/>
                    <label className="radiobtn">
                    <input type="radio" value="100,200" name="cuisine"/> 100-200
                    </label>
                    <br/>
                    <label className="radiobtn">
                    <input type="radio" value="200,300" name="cuisine"/> 200-300
                    </label>
                    <br/>
                    <label className="radiobtn">
                    <input type="radio" value="300,400" name="cuisine"/> 300-400
                    </label>
                    <br/>
                    <label className="radiobtn">
                    <input type="radio" value="400,500" name="cuisine"/> 400-500
                    </label>
                    
                    
      </div>
            </React.Fragment>
        )
    }
}
export default CostFilter;